import { atom } from 'recoil'
import { IAuth } from '../interfaces/auth.interface'

//      
const cashe = JSON.parse(localStorage.getItem('AUTH'))

//  
const authAtom = atom<IAuth>({
    key: 'authAtomKey',
    default: cashe || {
        idInstance: null,
        apiTokenInstance: null,
    },
})

export default authAtom
